import React, { Component } from 'react';
import CopyToClipboard from 'react-copy-to-clipboard';
import { message } from 'antd';

export default class ColorCircle extends Component {
  getTextStyle(light = true) {
    const { color } = this.props;
    return {
      background: color,
      color: light ? '#fff' : 'unset',
    };
  }

  onCopied = () => {
    const { color } = this.props;
    message.success(`Copied: ${color}`);
  };

  render() {
    const { color, name, desc, light } = this.props;
    return (
      <div className="color-content">
        <CopyToClipboard text={color} onCopy={this.onCopied} title="click to copy color">
          <div className="main-color-circle">
            <div className="main-color-circle-item" style={this.getTextStyle(light)}>
              <span className="main-color-circle-value">{color.toLowerCase()}</span>
            </div>
          </div>
        </CopyToClipboard>
        <div className="color-content-text">
          <p>{name}</p>
          <p>{desc}</p>
        </div>
      </div>
    );
  }
}
